<template>
    <div v-if="memberInfor.mobile">
        <div class="lipintao-side-toolbar">
            <a class="option-box" data-type="app">
                <!--<img src="../../assets/images/wechat.png" alt="" srcset="">-->
                <span class="show-txt">二维码</span>
                <div class="app-qr-box">
                    <div class="bg-box">
                        <div class="qr-item-box">
                            <img
                                :src="memberInfor.qrcode"
                                alt="客服微信号"
                            >
                            <p class="desc">客服微信号</p>
                        </div>
                    </div>
                </div>
            </a>
            <a class="option-box" data-type="app">
                <!--<img src="../../assets/images/mobile.png" alt="" srcset="">-->
                <span class="show-txt">手机号</span>
                <p class="mobile">{{memberInfor.mobile}}</p>
            </a>
        </div>
    </div>
</template>

<script>
export default {
  name: "kefu",
  data() {
    return {
        memberInfor:{
          qrcode: '',
          mobile: ''
        }
    }
  },
  created() {
    const timer = setInterval(() => {
      this.memberInfor = JSON.parse(sessionStorage.getItem('memberInfor')) || {
          qrcode: '',
          mobile: ''
        }
      console.log(this.memberInfor)
      if(this.memberInfor.mobile) clearInterval(timer)
    }, 1000)
  }
};
</script>

<style scoped>
.lipintao-side-toolbar {
  position: fixed;
  right: 8px;
  bottom: 160px;
  width: 44px;
  z-index: 1999;
}
.lipintao-side-toolbar a.option-box {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-bottom: 1px solid #e0e0e0;
  border-left: 1px solid #e0e0e0;
  border-right: 1px solid #e0e0e0;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
  background-color: #fff;
  text-align: center;
  height: 44px;
  cursor: pointer;
}
.lipintao-side-toolbar a.option-box:first-child {
  border-top: 1px solid #e0e0e0;
}
.lipintao-side-toolbar a.option-box[data-type="app"]:hover .app-qr-box {
  display: block;
}
.lipintao-side-toolbar a.option-box .app-qr-box {
  display: none;
  position: absolute;
  margin-right: 44px;
  right: 0;
  top: 0;
  padding-right: 4px;
}
.lipintao-side-toolbar a.option-box .app-qr-box .bg-box {
  padding: 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #e0e0e0;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}
.lipintao-side-toolbar a.option-box .app-qr-box .bg-box .qr-item-box {
  margin: 8px;
  padding: 8px;
  border: 1px solid #ebebeb;
  border-radius: 4px;
}
.lipintao-side-toolbar a.option-box .app-qr-box .bg-box .qr-item-box img {
  display: block;
  width: 120px;
  height: auto;
}
.lipintao-side-toolbar a.option-box .app-qr-box .bg-box .qr-item-box p {
  margin: 8px 0 0;
  font-size: 14px;
  color: #4d4d4d;
}
.lipintao-side-toolbar a.option-box.active img,
.lipintao-side-toolbar a.option-box:hover img {
  display: none;
}
.lipintao-side-toolbar a.option-box.active .show-txt,
.lipintao-side-toolbar a.option-box:hover .show-txt {
  opacity: 100;
}
.lipintao-side-toolbar a.option-box img {
  display: block;
  width: 22px;
  height: auto;
}
.lipintao-side-toolbar a.option-box .show-txt {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  font-size: 12px;
  line-height: 14px;
  background-color: #ca0c16;
  color: #fff;
  text-align: center;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}
.lipintao-side-toolbar a.option-box[data-type="app"]:hover .mobile{
  display: block;
}
.lipintao-side-toolbar .mobile{
  position: absolute;
  display: none;
  right: 45px;
  top: -10px;
  color: #4d4d4d;
  border-radius: 5px;
  background: #fff;
  padding: 8px 10px;
  border: 1px solid #e0e0e0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}
</style>




